import React from "react";

const Checkbox = (props) => {
  return (
    <div>
      <label>
        <input
          type="checkbox"
          value={props.value}
          onChange={(e) => props.handleChange(e)}
          label={props.label}
          name={props.name}
          id={props.id}
        />
        {props.value}
      </label>
    </div>
  );
};
export default Checkbox;
