import {
  Button,
  Card,
  CardContent,
  CardMedia,
  Typography
} from "@mui/material";
import AddShoppingCartIcon from "@mui/icons-material/AddShoppingCart";
import { useSnackbar } from "notistack";

const ProductCard = (props) => {
  let temparray = [];
  const { enqueueSnackbar } = useSnackbar();

  const addToCart = (e) => {
    temparray = props.cartItems;
    let checkvalue = false;

    if (temparray.length === 0) {
      temparray.push({
        item: props.obj,
        num: 1
      });
      checkvalue = true;
    } else {
      for (let i = 0; i < temparray.length; i++) {
        if (props.obj.id === temparray[i].item.id) {
          checkvalue = true;
          if (temparray[i].num === props.obj.quantity) {
            enqueueSnackbar("Exceed maximum no of allowed orders", {
              variant: "error"
            });
          } else {
            temparray[i].num += 1;
          }
          break;
        }
      }
    }
    if (checkvalue === false) {
      temparray.push({
        item: props.obj,
        num: 1
      });
      checkvalue = true;
    }

    props.updateCartItems(temparray);

    let tempcartitems = 0;
    props.cartItems.forEach((element) => {
      tempcartitems = tempcartitems + element.num;
    });
    props.updateNoOfCartItems(tempcartitems);
  };

  return (
    <Card className="card">
      <CardMedia component="img" image={props.obj.imageURL} alt="image" />
      <CardContent>
        <Typography>{props.obj.name}</Typography>
        <Typography>{props.obj.price}</Typography>
      </CardContent>
      <Button variant="contained" onClick={addToCart} id={props.obj.id}>
        <AddShoppingCartIcon /> ADD TO CART
      </Button>
    </Card>
  );
};

export default ProductCard;
