import "./styles.css";
//import Header from "./Header";
import ProductPage from "./ProductPage";
import { Routes, Route } from "react-router-dom";
import Cart from "./Cart";
import { useEffect, useState } from "react";

function App() {
  const [cartItems, updateCartItems] = useState([]);
  const [noOfCartItems, updateNoOfCartItems] = useState(0);

  return (
    <>
      <div className="App">
        <Routes>
          <Route
            path="/"
            element={
              <ProductPage
                cartItems={cartItems}
                updateCartItems={updateCartItems}
                noOfCartItems={noOfCartItems}
                updateNoOfCartItems={updateNoOfCartItems}
              />
            }
          />
          <Route
            path="cart"
            element={
              <Cart
                cartItems={cartItems}
                updateCartItems={updateCartItems}
                noOfCartItems={noOfCartItems}
                updateNoOfCartItems={updateNoOfCartItems}
              />
            }
          />
        </Routes>
      </div>
    </>
  );
}
export default App;
