import React, { useCallback, useEffect, useState } from "react";
import "./styles.css";
import ProductGrid from "./ProductGrid";
import Filters from "./Filters";
import Box from "@mui/material/Box";
import TextField from "@mui/material/TextField";
import axios from "axios";
import Header from "./Header";
import Button from "@mui/material/Button";

const ProductPage = (props) => {
  const [productList, updateProductList] = useState([]);
  const [searchValue, updateSearchValue] = useState("");
  const [debounceTimerId, setDebounceTimeId] = useState(0);
  const [filteredList, updateFilteredList] = useState([]);

  const performAPICall = async (sText) => {
    try {
      let url =
        "https://geektrust.s3.ap-southeast-1.amazonaws.com/coding-problems/shopping-cart/catalogue.json";
      let res = await axios.get(url);
      let temp = res.data;
      if (sText !== "") {
        let newarray = [];
        let temparray = sText.split(/\s+/);
        temp.forEach((obj) => {
          temparray.forEach((word) => {
            if (obj.name.toLowerCase().includes(word.toLowerCase())) {
              newarray.push(obj);
              return;
            }
          });
        });
        temp = newarray;
      }

      updateProductList(temp);
    } catch (error) {
      console.log(error);
    }
  };

  useEffect(() => {
    if (debounceTimerId !== 0) {
      clearTimeout(debounceTimerId);
    }
    const newtimerId = setTimeout(() => {
      performAPICall(searchValue);
    }, 500);
    setDebounceTimeId(newtimerId);
  }, [searchValue]);

  const performSearch = (text) => {
    updateSearchValue(text);
  };

  return (
    <>
      <Header
        cartItems={props.cartItems}
        noOfCartItems={props.noOfCartItems}
        updateNoOfCartItems={props.updateNoOfCartItems}
      />

      <div className="mainbody">
        <div className="searchbar">
          <Box mb={4} lg={12}>
            <TextField
              size="large"
              type="text"
              name="search-box"
              placeholder="Search for items/categories"
              fullWidth
              onChange={(e) => performSearch(e.target.value)}
            />
          </Box>
        </div>
        <div className="productpage">
          <div className="filters">
            <Filters
              productList={productList}
              updateProductList={updateProductList}
              filteredList={filteredList}
              updateFilteredList={updateFilteredList}
              cartItems={props.cartItems}
              updateCartItems={props.updateCartItems}
            />
          </div>

          <div className="productgrid">
            <Box>
              <ProductGrid
                listofproducts={productList}
                cartItems={props.cartItems}
                updateCartItems={props.updateCartItems}
                noOfCartItems={props.noOfCartItems}
                updateNoOfCartItems={props.updateNoOfCartItems}
              />
            </Box>
          </div>
        </div>
      </div>
    </>
  );
};

export default ProductPage;
