import React from "react";
import Box from "@mui/material/Box";
import Button from "@mui/material/Button";
import "./styles.css";
import ShoppingCartIcon from "@mui/icons-material/ShoppingCart";
import { useLocation, useNavigate } from "react-router-dom";
import ShoppingCartCheckoutIcon from "@mui/icons-material/ShoppingCartCheckout";

const Header = (props) => {
  const location = useLocation();
  let navigate = useNavigate();

  const clicked = (event) => {
    console.log(location);
    if (event.target.value === "products") navigate("/");
    else if (event.target.value === "cart" && location.pathname !== "/cart")
      navigate("cart");
  };

  return (
    <>
      <Box className="header">
        <Box className="header-title">
          <p>
            <h2>TeeRex Store</h2>
          </p>
        </Box>
        <div className="icons">
          <Button className="products" value="products" onClick={clicked}>
            products
          </Button>
          <Button value="cart" onClick={clicked} variant="contained">
            <ShoppingCartCheckoutIcon /> CART {props.noOfCartItems}
          </Button>
        </div>
      </Box>
    </>
  );
};
export default Header;
