import React, { useState, useEffect } from "react";
import Header from "./Header";
import CartItem from "./CartItem";
import App from "./App";
import ProductPage from "./ProductPage";
import Grid from "@mui/material/Grid";
import "./styles.css";
import { SentimentDissatisfied } from "@mui/icons-material/SentimentDissatisfied";

const Cart = (props) => {
  const [totalCartValue, updateTotalCartValue] = useState(0);

  useEffect(() => {
    if (props.cartItems.length !== 0) {
      let tempvalue = 0;
      props.cartItems.forEach((element) => {
        tempvalue = tempvalue + element.item.price * element.num;
      });
      updateTotalCartValue(tempvalue);
    }
  }, [totalCartValue, props.cartItems]);
  return (
    <>
      <Header noOfCartItems={props.noOfCartItems} />
      <div>
        <h1>Shopping Cart</h1>
      </div>
      <div className="cart">
        {props.cartItems.length === 0 ? (
          <div>Nil items</div>
        ) : (
          props.cartItems.map((item) => {
            return (
              <CartItem
                key={item.id}
                item={item}
                cartItems={props.cartItems}
                updateCartItems={props.updateCartItems}
                totalCartValue={totalCartValue}
                updateTotalCartValue={updateTotalCartValue}
                noOfCartItems={props.noOfCartItems}
                updateNoOfCartItems={props.updateNoOfCartItems}
              />
            );
          })
        )}
        <div>
          <hr></hr>
        </div>
        <div className="total">
          <div>
            <h3>Cart Total</h3>
          </div>
          <div>
            <h2>{totalCartValue}</h2>
          </div>
        </div>
      </div>
    </>
  );
};
export default Cart;
