import React, { useEffect, useState } from "react";
import Button from "@mui/material/Button";
import "./styles.css";
import Box from "@mui/material/Box";
import { useSnackbar } from "notistack";

const CartItem = (props) => {
  const [itemtotal, updateItemTotal] = useState(
    props.item.item.price * props.item.num
  );
  let localitem = props.item;
  let temparray = props.cartItems;
  const index = temparray.indexOf(localitem);
  const { enqueueSnackbar } = useSnackbar();

  const cartbuttonClicked = (e) => {
    if (e.target.value === "+") {
      if (localitem.num === localitem.item.quantity) {
        enqueueSnackbar("Exceeded max number of allowed items", {
          variant: "error"
        });
      } else {
        temparray[index].num += 1;
        updateItemTotal(localitem.item.price * localitem.num);
        props.updateCartItems(temparray);
      }
    } else if (e.target.value === "-") {
      if (localitem.num === 1) {
        if (temparray.length === 1) {
          temparray = [];
          props.updateCartItems(temparray);
          props.updateTotalCartValue(0);
        } else {
          temparray.splice(index, 1);
          props.updateCartItems(temparray);
        }
      } else {
        temparray[index].num -= 1;
        updateItemTotal(localitem.item.price * localitem.num);
        props.updateCartItems(temparray);
      }
    }
    if (temparray.length !== 0) {
      let tempvalue = 0;
      props.cartItems.forEach((element) => {
        tempvalue = tempvalue + element.item.price * element.num;
      });
      props.updateTotalCartValue(tempvalue);
    }

    let tempcartitems = 0;
    props.cartItems.forEach((element) => {
      tempcartitems = tempcartitems + element.num;
    });
    props.updateNoOfCartItems(tempcartitems);
  };

  return (
    <Box className="cartitem">
      <div className="cartimage">
        <img src={props.item.item.imageURL} alt="none" height="200px" />
      </div>
      <div className="itemdetails">
        <div className="title">
          <h3>{props.item.item.name}</h3>
        </div>
        <div>
          <div className="cartbuttons">
            <div>
              <Button onClick={cartbuttonClicked} value="-">
                -
              </Button>
            </div>
            <div>{props.item.num}</div>
            <div>
              <Button onClick={cartbuttonClicked} value="+">
                +
              </Button>
            </div>
            <div>
              {" "}
              <h3>Total : {itemtotal} </h3>
            </div>
          </div>
        </div>
      </div>
    </Box>
  );
};

export default CartItem;
